var FloatTop = React.createClass({

    componentDidMount: function() {
        $(".floatTop").click(function(){
            $("html,body").animate({scrollTop:0},800);
            return false;
        });
    },

    render: function(){
        return(
            <li>
                <a id="floatTop" className="floatTop">
                    <span>TOP</span>
                </a>
            </li>
        );
    }
});


module.exports = FloatTop;



