var HomeSliderBanner = require('./HomeSliderBanner.react');
var HomeShopUS = require('./HomeShopUS.react');
var HomeLifeProject = require('./HomeLifeProject.react');
var HomeStore = require('./HomeStore.react');
var HomeCategoryMap = require('./HomeCategoryMap.react');
var HomeBankInfo = require('./HomeBankInfo.react');



var HomeTabOne = React.createClass({

  componentWillUnmount: function() {
      
  },

  render: function() {
          return (
              <li className={"current"} style={{display: "block"}}>
                  {/*<!-- bnSlider start -->*/}
                  <HomeSliderBanner />
                  {/*<!-- bnSlider end -->*/}

                  {/*<!-- shopUs start -->*/}
                  <HomeShopUS />
                  {/*<!-- shopUs end -->*/}

                  {/*<!-- lifeProject start -->*/}
                  <HomeLifeProject />
                  {/*<!-- lifeProject end -->*/}

                  {/*<!-- IndexCateShow start -->*/}
                  <HomeStore />
                  {/*<!-- IndexCateShow end -->*/}

                  {/*<!-- catesMap start -->*/}
                  <HomeCategoryMap />
                  {/*<!-- catesMap end -->*/}

                  {/*<!-- banksInfo start -->*/}
                  <HomeBankInfo />
                  {/*<!-- banksInfo end -->*/}
              </li>
          );
      }

});

module.exports = HomeTabOne;



