var HomeTabOne = require('./HomeTabOne.react');
var Loading = require('../common/Loading.react');
/*var HomeTabTwo = require('./HomeTabTwo.react');
var HomeTabThree = require('./HomeTabThree.react');*/

/*var TrackAction = require('../../actions/cart/TrackAction');*/
var TrackAction = nemReact.require('TrackAction');
var FloatRightBtn = require('../floatRightBtn/FloatRightBtn.react');


var TAB_01 = "tab01";
var TAB_02 = "tab02";
var TAB_03 = "tab03";

var HomeContent = React.createClass({

    /*testDis: function(){
      TrackAction.getCart([], []);
    },*/

    clickActiveTab: function(tab) {
        if(this.state.activeTab == tab){
          return ;
        }
        /*var cartNum = this.testDis();*/
        var _setActiteTab = this.setActiveTab;
        switch (tab) {
            case TAB_01:
                this.setState({activeTab: TAB_01});
                _setActiteTab(<Loading/>);
                _setActiteTab(<HomeTabOne/>);
                break;
            case TAB_02:
                this.setState({activeTab: TAB_02});
                _setActiteTab(<Loading/>);
                nemReact.initJS([
                    {
                        key: 'homeFlash',
                        cb: function() {
                            _setActiteTab(nemReact.require('homeFlash', 'react').getComponent());
                        }
                    }
                ]);
                break;
            case TAB_03:
                this.setState({activeTab: TAB_03});
                _setActiteTab(<Loading/>);
                nemReact.initJS([
                    {
                        key: 'homeHotSale',
                        cb: function() {
                            _setActiteTab(nemReact.require('homeHotSale', 'react').getComponent());
                        }
                    }
                ]);
                break;
            default:
                this.setState({activeTab: TAB_01});
                _setActiteTab(<Loading/>);
                _setActiteTab(<HomeTabOne/>);
        }
    },

    setActiveTab: function(obj) {
        this.setState({tabContent: obj});
    },

    getActiveTabClass: function(tab) {
        var className = "";
        if (this.state.activeTab == tab) {
            className = "current";
        }
        return className;
    },

    getInitialState: function() {
        return {activeTab: TAB_01, tabContent: <HomeTabOne/>};
    },

    render: function() {
        return (
            <div className="mainPage">
                <div className="Indextabs">
                    <label htmlFor="IndexTab01" className={this.getActiveTabClass(TAB_01)} onClick={this.clickActiveTab.bind(this, TAB_01)}>首頁</label>
                    <label htmlFor="IndexTab02" className={this.getActiveTabClass(TAB_02)} onClick={this.clickActiveTab.bind(this, TAB_02)}>天天閃購</label>
                    <label htmlFor="IndexTab03" className={this.getActiveTabClass(TAB_03)} onClick={this.clickActiveTab.bind(this, TAB_03)}>熱銷排行榜</label>
                </div>
                <ul className="IndexTabContent" id="IndexTabContent">
                    {this.state.tabContent}
                </ul>
                <FloatRightBtn />
            </div>
        );
    }
});

module.exports = HomeContent;



