//生活提案上方主題名稱
var HomeLifeProject = React.createClass({

    getInitialState: function() {
        return {
            lifeProjectData: [],
            moreLink: ""
        };
    },

    componentWillMount: function() {
        HomeLifeProjectStore.addChangeListener(this._onChange);
        // // this.setState({ lifeProjectData: getLifeProjectData()});
    },

    componentDidMount: function(){
        HomeLifeProjectAction.getData();
    },

    componentWillUnmount: function(){
        HomeLifeProjectStore.removeChangeListener(this._onChange);
    },

    render: function() {
        var lifeProjectData = this.state.lifeProjectData;
        // console.log(this.state.lifeProjectData.listContent);
        // (!!lifeProjectData.listContent)
        // ?lifeProjectData.listContent.map(function(item,index){
        //   console.log(item.tabTitle);
        // })
        // :null
        return (
            <div className="module lifeProject">
              <h3 className="title">
                  <span>生活提案</span>
                  <a className="moreLink" href={this.state.lifeProjectData.moreLink}>查看更多</a>
              </h3>
              <div className="moduleContent">
                <input type="radio" name="lifeGroup" id="life01" hidden={{true}} defaultChecked={{true}} />
                <input type="radio" name="lifeGroup" id="life02" hidden={{true}} />
                <input type="radio" name="lifeGroup" id="life03" hidden={{true}} />
                <input type="radio" name="lifeGroup" id="life04" hidden={{true}} />
                <ul className="lifeNavs">
                {
                  (!!lifeProjectData.listContent)
                  ?lifeProjectData.listContent.map(function(item,index){
                    return <li key={index}><label htmlFor={"life0" + (index + 1)}>{item.tabTitle}</label></li>
                  }, this)
                  :null
                }
                </ul>
                <div className="lifeNavContent">
                {
                  (!!lifeProjectData.listContent)
                  ?lifeProjectData.listContent.map(function(item, index){
                    return <LifeProjectContent LifeContentData={item.sub} key={index} />
                  },this)
                  :null
                }
                </div>
              </div>
            </div>
        );
    },

    _onChange: function(){
      //console.log(HomeLifeProjectStore.getData());
      this.setState({ lifeProjectData: HomeLifeProjectStore.getData() });
    }



});

module.exports = HomeLifeProject;



